  .cardContainer:hover .overlay{
    /* background:rgb(0,0,0); */
    /* z-index: 10; */
    opacity: 1;
  }
  .cardContainer:hover .textOverlay{
      color:rgb(255, 0, 103);
  }

  .cardContainer:hover .eachImage{
    transform: scale(1.2);
    transition: 1s;
    /* width: 120%; */
    
  }

.imgContainer{
    overflow: hidden;
    position: relative;
    width: 100%;
    
    
}

.eachImage{
    width: 100%;
    display: block;
    cursor: pointer;
    animation: fadein 2s;
    transition: 2s;
    
    
  }

  .overlay{
    /* overflow: visible; */
    position: absolute;
    top: 0;
    /* bottom: 0; */
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
  background-repeat: no-repeat;
    opacity: 0;
    transition: .5s ease;
    background-color: rgba(0, 0, 0, 0.4);
    background-image: url('/portfolio/assets/GridImg/grid4.png');
  }
  .textOverlay{
      width: 70%;
      background: white;
      opacity: 1;
      position: absolute;
      /* top: 0; */
      bottom: 0px;
      left: 0;
      right: 0;
      text-align: left;
      padding-left: 5px;
  }
